//   KeyPoints:   (/questions/{id}/keypoints, /keypoints/{id}) 
export const getKeyPoints = async ({ questionId, token } = {}) => {
  if (questionId == null) return null;
  const tryPaths = [
    `/questions/${questionId}/keypoints`,
    `/keypoints/${questionId}`,
  ];
  for (const p of tryPaths) {
    try {
      const data = await http(p, {}, token);
      if (data) return data;
    } catch (_) { /* try next */ }
  }
  return null;
};

