// src/components/BackendStatusDemo.js -     
import React from 'react';
import { useBackendStatus, BackendStatusIndicator } from '../hooks/useBackendStatus.js';
import { getQuiz, submitAnswer, getProgress, getBadges } from '../api/explore.js';

const BackendStatusDemo = () => {
  const { isConnected, isLoading, recheckStatus } = useBackendStatus();

  const testAPI = async () => {
    console.log('=== API   ===');
    
    try {
      //   
      console.log('1.    ');
      const quizResult = await getQuiz(1);
      console.log(' :', quizResult);
      
      //   
      console.log('2.   ');
      const submitResult = await submitAnswer(1, 1);
      console.log(' :', submitResult);
      
      //   
      console.log('3.   ');
      const progressResult = await getProgress();
      console.log(' :', progressResult);
      
      //   
      console.log('4.   ');
      const badgesResult = await getBadges();
      console.log(' :', badgesResult);
      
      console.log('=== API   ===');
    } catch (error) {
      console.error('API   :', error);
    }
  };

  return (
    <div style={{ 
      padding: '20px', 
      border: '1px solid #ddd', 
      borderRadius: '8px', 
      margin: '20px',
      backgroundColor: '#f9f9f9'
    }}>
      <h3>    </h3>
      
      {/*   */}
      <div style={{ marginBottom: '15px' }}>
        <BackendStatusIndicator showText={true} />
      </div>
      
      {/*   */}
      <div style={{ marginBottom: '15px' }}>
        <p><strong> :</strong> {isLoading ? ' ...' : (isConnected ? ' ' : '   ')}</p>
        <p><strong> :</strong> {isConnected ? '  API' : '  (testData.js)'}</p>
      </div>
      
      {/*  */}
      <div style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
        <button 
          onClick={recheckStatus}
          style={{
            padding: '8px 16px',
            backgroundColor: '#007bff',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
              
        </button>
        
        <button 
          onClick={testAPI}
          style={{
            padding: '8px 16px',
            backgroundColor: '#28a745',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
           API  
        </button>
      </div>
      
      {/*  */}
      <div style={{ 
        marginTop: '15px', 
        padding: '10px', 
        backgroundColor: '#e9f7ff', 
        borderRadius: '4px',
        fontSize: '14px'
      }}>
        <h4>  :</h4>
        <ul style={{ margin: '5px 0', paddingLeft: '20px' }}>
          <li><strong> :</strong>   API </li>
          <li><strong>  :</strong>    </li>
          <li><strong> :</strong>   30    </li>
          <li><strong> :</strong> "   "     </li>
        </ul>
      </div>
    </div>
  );
};

export default BackendStatusDemo;